@import '_fonts';
@include google-font("Lato");

body{
  margin: 0;
  font-family: $text-font; 
}
.wrapper{
  max-width: 960px;
  margin: 0 auto;
}
  h1{
    margin: 40px auto;
    color: #ff780a;
    text-align: center;
  }

ul{
  padding: 0;
  color: #ff780a;
  text-transform: capitalize;
  list-style-type: none;
}

li{
  padding: 6px 10px;
  border: 1px solid #eee;
  margin: 10px auto;
}

li h4{
  color: #ff0aa7;
  margin: 0;
  text-transform: capitalize;
}
  li p{
  color: #333;
  margin: 8px 0 0;
  font-size: large;
  }  

footer{
  background: #eee;
  padding: 60px;
  margin-top: 60px;
}

form{
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
}

label{
  display: block;
  font-weight: bold;
  font-size: 0.8em;
  color: #333;
  margin: 16px 0 6px;
}
  input, select{
    padding: 6px;
    border: 1px solid #e1e1e1;
    border-radius: 4px;
  }

.field{
  display: inline-block;
  margin: 0 10px;
}

button{
  color: white;
  border: 0;
  background: #ff780a;
  padding: 6px;
  min-width: 80px;
  border-radius: 4px;
}

footer a{
  text-align: center;
  display: block;
  color: #999;
  margin-top: 40px;
  font-size: 0.7em;
}